import { Component, Input } from '@angular/core';
import { Observable } from 'rxjs';
import { map, skipWhile } from 'rxjs/operators';
import { User } from 'src/app/common/models/user.model';
import { ApplicationContextQuery } from 'src/app/common/store/application-context/application-context.query';
import { ApplicationContextStore } from 'src/app/common/store/application-context/application-context.store';

@Component({
    selector: 'epop-header-bar-user-info',
    styleUrls: ['header-bar-user-info.component.scss'],
    templateUrl: 'header-bar-user-info.component.html'
})
export class HeaderBarUserInfoComponent extends ApplicationContextQuery {
    @Input() menuOpen: boolean;

    public userRole$: Observable<string> = this.getUserRole();
    public getUserAvatar$: Observable<string> = this.getUserAvatar();
    public userInfoInline$: Observable<string> = this.getUserInfoInline();

    public avatarUrl: string;

    constructor(protected store: ApplicationContextStore) {
        super(store);
    }

    private getUserRole(): Observable<string> {
        return this.getCurrentUserRole().pipe(
            map((userRole: string) =>
                userRole || 'unkown'
            )
        );
    }

    private getUserAvatar(): Observable<string> {
        return this.getCurrentUserObservable().pipe(
            map((currentUser: User) =>
                currentUser?.getAvatarUrl() || undefined
        ));
    }

    private getUserInfoInline(): Observable<string> {
        return this.getCurrentUserObservable().pipe(
            skipWhile(currentUser => !currentUser),
            map((currentUser: User) =>
                !!currentUser ?
                `${currentUser.getUsername()} - ${currentUser.getMatricule()} (${currentUser.getVisa()})` :
                ''
            )
        );
    }
}
