
export interface ITextMatchableObject {
  getSearchableText(): string;
}

export class StringHelper {

  /**
   * Convert a percentage number to a string.
   *
   * @param percentage between 1 and 100.
   * @returns string with '%' symbol. For example : '22%'.
   */
  static percentageToString(percentage: number): string {
    // Use negative incremental index for generated ID to prevent conflict with APIs :
      return `${percentage}%`;

  }

  static matchTextSearch(matchObject: ITextMatchableObject, searchText: string): boolean {
      const textToFilter = matchObject.getSearchableText();
      return searchText === '' || textToFilter.toLowerCase().includes(searchText.toLowerCase());
  }

  static capitalizeFirstLetter(value: string): string {
    return value.charAt(0).toLocaleUpperCase() + value.slice(1);
  }

  /**
   * Check if a string is numerical (integer) or not.
   *
   * @param charStr string value to check
   * @returns true or false
   */
  static isStringNumeric(charStr: string): boolean {
    return !!/\d+/.test(charStr);
  }
}
