
export abstract class Route {
    /**
     * Generate URL path to access the route. Can be use with RouterLink.
     */
    abstract path: (...arg: Array<any>) => string;

    /**
     * Get the router path to configure the route in routing module.
     */
    abstract routerPath?: string;
}

export class RouteResolver<T extends Record<string, Route>, K extends keyof T> {

    private routes: T;

    private basePath: string;

    constructor(configuration: T) {
        this.routes = configuration;
    }

    /**
     * Update the base path of the resolver and return it.
     * Useful when defining Anuglar routes in the routing module.
     *
     * @param basePath
     */
    public configureBasePath(basePath: string): string {
        this.basePath = basePath;
        return basePath;
    };

    /**
     * Get the complete path of a specified route. The configured basePath will be prefixed.
     *
     * @param routeKey
     */
    public routePath(routeKey: K, ...args: Parameters<T[K]['path']>): string {
        return this.basePath + '/' + this.routes[routeKey].path(args);
    }

    /**
     * Get the route associated to a key.
     *
     * @param routeKey
     */
    public route(routeKey: K): Route {
        return this.routes[routeKey];
    }

    public baseRoutePath(): string {
        return this.basePath;
    }
}
