import { RouteResolver } from 'src/app/common/core/routing/models/routeResolver.model';

export const UnitsRouteResolver = new RouteResolver({
    units: {
        path: () => 'units',
        routerPath: 'units',
    },
    details: {
        path: (unitId: string) =>  unitId,
        routerPath: ':unitId'
    },
});
