import { EpopNormalizedData } from './epop-normalized-model.definition';
import { HttpClient } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import { Observable, throwError } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Schema, normalize } from 'normalizr';
import { ClazzOrModelSchema, deserialize } from 'serializr';

export abstract class EpopService {
    private apiUrl = `${environment.api.url}/`;

    constructor(
        protected httpClient: HttpClient
    ) {}

    protected getApiUrl(): string {
        return this.apiUrl;
    }

    protected getResourceUrl(): string {
        return this.getApiUrl() + this.getResourcePath();
    }

    protected getUrlAppend(): string {
        return (!!environment.api.urlStringAppend ?  environment.api.urlStringAppend  : '');
    }

    protected buildUrl(path: string): string {
        return this.getResourceUrl() +  (!!path ?  `${path}` : '') + this.getUrlAppend();
    }

    /**
     * Get normalized data of entity. Useful to store complex related entities.
     *
     * @param entityType
     * @param normalizationSchema
     * @param entityId
     */
    getNormalizedEntity<T>(
        entityType: ClazzOrModelSchema<T>,
        normalizationSchema: Schema<T>,
        entityId?: string,
        path?: string, subPath?: string): Observable<EpopNormalizedData> {
        return this.getEntity<T>(entityType, entityId, path, subPath).pipe(
            map(response => normalize(response, normalizationSchema)),
        );
    }

    /**
     * Request and parse a single entity from current resourcePath : apiUrl/path/entityId/subPath
     *
     * @param entityType Type of entity requested
     * @param normalizationSchema Definition of schema for normalization. If not defined, no normalization is done.
     */
    getEntity<T>(entityType: ClazzOrModelSchema<any>, entityId?: string, path?: string, subPath?: string): Observable<T> {
        const url = this.buildUrl(
            (!!path ?  `/${path}` : '')
            + (!!entityId ?  `/${entityId}` : '')
            +  (!!subPath ?  `/${subPath}` : '')
        );
        return this.httpClient
            .get(url)
            .pipe(
                map(response => deserialize<T>(entityType, response)),
                catchError(error => throwError(error))
            );
    }

    /**
     * Define API resource path like 'userInfo' or 'plannings'.
     * This path will be concatenate to env api URL.
     */
    public abstract getResourcePath(): string;
}
