import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppUtils } from '../../core/app-utils.service';
import { MatIconModule } from '@angular/material/icon';
import { AppIconsDefinition } from './app-icons.definition';

@NgModule({
    exports: [
        MatIconModule
    ],
    declarations: [
    ],
    imports: [
        CommonModule,
        MatIconModule,
    ]
})
export class AppIconsModule {
    constructor(
        private appUtils: AppUtils
    ) {
        this.appUtils.addSvgIcons(AppIconsDefinition);
    }
}
