import { Route, RouteResolver } from 'src/app/common/core/routing/models/routeResolver.model';

export const PlanningGeneratorRouteResolver = new RouteResolver({
    generate: {
        path: () => 'generate'
    } as Route,
    unit: {
        path: (unitId) => unitId,
        routerPath: ':unitId'
    },
});
