import { PlanningGeneratorRouteResolver } from './../../../planning-generator/planning-generator-routing.resolver';
import { Component } from '@angular/core';
import { ApplicationContextQuery } from 'src/app/common/store/application-context/application-context.query';
import { HomeRouteResolver } from 'src/app/pages/home/home-routing.resolver';
import { PlanningRouteResolver } from 'src/app/pages/planning/planning-routing.resolver';
import { UnitsRouteResolver } from 'src/app/pages/units/units-routing.resolver';

import { ROLE_COLLAB_ID, ROLE_PLANNER_ID } from 'src/app/common/models/role.model';
import { map, skipWhile } from 'rxjs/operators';
import { ApplicationContextStore } from 'src/app/common/store/application-context/application-context.store';
import { Observable } from 'rxjs';

interface LeftBarItem {
    icon: string;
    label: string;
    path?: string;
    rights?: string[];
}

@Component({
    selector: 'epop-left-bar',
    styleUrls: ['left-bar.component.scss'],
    templateUrl: 'left-bar.component.html',
})
export class LeftBarComponent extends ApplicationContextQuery {

    public getMenuList$: Observable<LeftBarItem[]> = this.getMenuList();

    public leftBarItems: LeftBarItem[] = [
        {
            icon: 'mdi/home-variant-outline',
            label: 'home',
            path: HomeRouteResolver.baseRoutePath()
        },
        {
            icon: 'mdi/calendar-edit',
            label: 'create',
            path: PlanningGeneratorRouteResolver.baseRoutePath(),
            rights: [ROLE_PLANNER_ID]
        },
        {
            icon: 'mdi/shape-outline',
            label: 'delegations',
            path: PlanningRouteResolver.baseRoutePath(),
            rights: [ROLE_PLANNER_ID]
        },
        {
            icon: 'mdi/account-group-outline',
            label: 'teams',
            path: UnitsRouteResolver.baseRoutePath(),
            rights: [ROLE_PLANNER_ID]
        },
        {
            icon: 'mdi/timelapse',
            label: 'planning',
            rights: [ROLE_COLLAB_ID]
        },
        {
            icon: 'mdi/calendar-range',
            label: 'myTime',
            rights: [ROLE_COLLAB_ID]
        },
        {
            icon: 'mdi/comment-text-multiple-outline',
            label: 'communications'
        },
        {
            icon: 'mdi/bell-ring-outline',
            label: 'alerts',
            rights: [ROLE_PLANNER_ID]
        },
        {
            icon: 'mdi/folder-outline',
            label: 'files',
            rights: [ROLE_COLLAB_ID]
        },
        {
            icon: 'mdi/account-circle-outline',
            label: 'profil',
            rights: [ROLE_COLLAB_ID]
        },
        {
            icon: 'mdi/cog',
            label: 'settings',
            rights: [ROLE_COLLAB_ID]
        },
    ];

    public showNavLabels = false;
    private showNavLabelsClicked = false;
    private showNavLabelsHover = false;

    constructor(protected store: ApplicationContextStore) {
        super(store);
    }

    public getMenuList(): Observable<LeftBarItem[]> {
        return this.getCurrentUserRole().pipe(
            skipWhile(role => !role),
            map((role: string) => this.leftBarItems.filter(
                (item: LeftBarItem) => !item.rights || item.rights.includes(role)
            ))
        );
    }

    public setShowNavLabel(): void {
        this.showNavLabels = this.showNavLabelsClicked || this.showNavLabelsHover;
    }

    public toggleLabels(): void {
        this.showNavLabelsClicked = !this.showNavLabelsClicked;
        this.setShowNavLabel();
    }

    public hoverLabel(isOpen: boolean): void {
        this.showNavLabelsHover = isOpen;
        this.setShowNavLabel();
    }
}
