import { catchError, tap } from 'rxjs/operators';
import { UserService } from '../../services/epop-api/user.service';
import { Injectable } from '@angular/core';
import { Store, StoreConfig } from '@datorama/akita';
import { ApplicationContext } from 'src/app/common/models/application-context.model';
import { Role, ROLE_PLANNER_ID } from 'src/app/common/models/role.model';
import { User } from 'src/app/common/models/user.model';
import { of } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
@StoreConfig({ name: 'applicationContextStore' })
export class ApplicationContextStore extends Store<ApplicationContext> {

    constructor(private usersService: UserService) {
        super(ApplicationContextStore.createInitialState());
        this.fetchUser();
    }


    static createInitialState(): ApplicationContext {
        return {
            currentUser: undefined,
            currentUserRoleCode: undefined
        };
    }

    public setCurrentUser(user: User, currentUserRoleCode: string) {
        this.update(state => ({
            ...state,
            currentUserRoleCode,
            currentUser: user,
        }));
    }

    public selectRole(roleToUse: string) {
        const user = this.getValue().currentUser;
        if (!user) { return; }
        const roles = user.getRoles();
        const newRole = roles.find(role => role.getCode() === roleToUse);
        if (newRole) {
            this.update(state => ({
                ...state,
                currentUserRoleCode: newRole.getCode()
            }));
        }
    }

    private fetchUser() {
        this.usersService.getCurrentUser().pipe(
            tap(user => {
                // By default, use PLANNER role if available
                const currentRole: Role = user.getRoles().find(role => role.getCode() === ROLE_PLANNER_ID) || user.getRoles()[0];
                this.setCurrentUser(user, currentRole?.getCode());
            }),
            catchError(error => {
                this.setCurrentUser(null, null);
                return of(error);
            })
        ).subscribe();
    }
}
