import { MainLayoutStore } from './main-layout.store';
import { MainLayoutState } from '../models/main-layout-state.model';
import { Query } from '@datorama/akita';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class MainLayoutQuery extends Query<MainLayoutState> {

    constructor(protected store: MainLayoutStore) {
        super(store);
    }

    getPageTitle() {
        return this.select('pageTitle');
    }

    getBackLinkPath() {
        return this.select('backLinkPath');
    }
}
