import { Component, HostListener } from '@angular/core';
import { ICellRendererAngularComp } from 'ag-grid-angular';
import { ICellRendererParams } from 'ag-grid-community';

export type CheckboxCellParams = ICellRendererParams & {
  defaultChecked: boolean;
  stopClickEventPropagation: boolean;
  onCheckboxChange: (isChecked: boolean, cellParams: ICellRendererParams) => void;
};

@Component({
  selector: 'epop-checkbox-cell',
  templateUrl: './checkbox-cell.component.html',
  styleUrls: ['./checkbox-cell.component.scss']
})
export class CheckboxCellComponent implements ICellRendererAngularComp {

  public params: CheckboxCellParams;
  public checked: boolean;

  constructor() {}

  public agInit(params: CheckboxCellParams): void {
    this.params = params;
    this.params.node.selectable = false;
    this.checked = params.defaultChecked;
  }

  public onStateChanged(event: Event): void {
    const element = event.target as HTMLInputElement;
    this.params.onCheckboxChange(element.checked, this.params);
    this.stopPropagationIfEnable(event);
  }

  public setCheckboxState(isChecked: boolean) {
    this.checked = isChecked;
  }

  public refresh(): boolean {
    // By returning false, we let the grid refresh the cell for us.
    return false;
  }

  @HostListener('mousedown', ['$event'])
  onMouseDown(e) {
    this.stopPropagationIfEnable(e);
  }

  private stopPropagationIfEnable(event: Event) {
    if(this.params.stopClickEventPropagation) {
      event.stopPropagation();
    }
  }
}
