import { MainLayoutState } from '../models/main-layout-state.model';
import { Injectable } from '@angular/core';
import { EntityStore, StoreConfig } from '@datorama/akita';

@Injectable({
  providedIn: 'root'
})
@StoreConfig({ name: 'mainLayoutStore' })
export class MainLayoutStore extends EntityStore<MainLayoutState> {

    constructor() {
        super(MainLayoutStore.createInitialState());
    }

    static createInitialState(): MainLayoutState {
        return {
            pageTitle: '',
            backLinkPath: null
        };
    }

    public setPageTitle(title: string) {
      this.update(state => ({
        ...state,
        pageTitle: title
      }));
    }

    public setBackLinkPath(backLinkPath: string) {
      this.update(state => ({
        ...state,
        backLinkPath
      }));
    }
}
