import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Observable, Subscription } from 'rxjs';

export type PageHeaderAction = {
  onClick: () => void;
  icon: string;
  isDisabled: () => boolean;
  position: 'left' | 'right';
  type?: 'default' | 'big' | 'medium';
};

export type PageHeaderSearch = {
  onValueChanges: (val: string) => void;
  icon: string;
  placeholder: Observable<string>;
};

@Component({
  selector: 'epop-page-container-header',
  templateUrl: './page-container-header.component.html',
  styleUrls: ['./page-container-header.component.scss']
})
export class PageContainerHeaderComponent implements OnInit, OnDestroy {

  @Input()
  protected headerActions: Array<PageHeaderAction>;
  @Input()
  public headerSearch: PageHeaderSearch;

  @Output()
  protected searchValue = new EventEmitter<string>();

  public searchForm = new FormGroup({
    search: new FormControl(),
  });

  private searchFormSubscription: Subscription;

  constructor() { }

  ngOnInit(): void {
    this.searchFormSubscription = this.searchForm.valueChanges.subscribe(val => {
      this.searchValue.emit(val.search);
    });
  }

  ngOnDestroy(): void {
    this.searchFormSubscription.unsubscribe();
  }

  getActions(position: PageHeaderAction['position']) {
    return this.headerActions.filter(a => a.position === position);
  }

  hasSearch(): boolean {
    return !!this.headerSearch;
  }

  onSubmit() {}

}
