import { StringHelper } from 'src/app/common/helpers/StringHelper';
import { Component, ViewChild, ViewContainerRef } from '@angular/core';
import { AgEditorComponent } from 'ag-grid-angular';
import { ICellEditorParams, IAfterGuiAttachedParams } from 'ag-grid-community';
import { AgGridHelper } from '../../helpers/ag-grid.helper';

const EMPTY_VALUE = 0;

@Component({
  selector: 'epop-numeric-cell-editor',
  templateUrl: './numeric-cell-editor.component.html',
  styleUrls: ['./numeric-cell-editor.component.scss']
})
export class NumericCellEditorComponent implements AgEditorComponent {

  public value: number;

  @ViewChild('input', {read: ViewContainerRef}) public input: ViewContainerRef;

  constructor() { }

  public agInit(params: ICellEditorParams): void {
    this.value = this.getInitialValue(params);
  }

  public getInitialValue(params: ICellEditorParams): number {
    let startValue = params.value;
    const keyPressBackspaceOrDelete = params.keyPress === AgGridHelper.KEY_BACKSPACE || params.keyPress === AgGridHelper.KEY_DELETE;
    if (keyPressBackspaceOrDelete) {
        startValue = EMPTY_VALUE;
    } else if (params.charPress && StringHelper.isStringNumeric(params.charPress)) {
        startValue = params.charPress;
    }

    if (startValue !== null && startValue !== undefined) {
        return startValue;
    }

    return EMPTY_VALUE;
}

  public getValue(): number {
    return this.value;
  }


  public focusIn?(): void {
    const htmlElement = this.getGui();
    htmlElement.focus();
    htmlElement.select();
  }

  public afterGuiAttached?(params?: IAfterGuiAttachedParams): void {
    this.focusIn();
  }

  public getGui(): HTMLInputElement {
    return this.input.element.nativeElement;
  }


}
