import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { MainLayoutQuery } from './store/main-layout.query';

@Component({
    selector: 'epop-main-layout',
    styleUrls: ['main-layout.component.scss'],
    templateUrl: 'main-layout.component.html'
})
export class MainLayoutComponent implements OnInit{

    public pageTitle$: Observable<string>;
    public backLinkPath$: Observable<string>;

    constructor(private mainLayoutQuery: MainLayoutQuery) {
    }

    ngOnInit(): void {
        this.pageTitle$ = this.mainLayoutQuery.getPageTitle();
        this.backLinkPath$ = this.mainLayoutQuery.getBackLinkPath();
    }
}
