import { ApplicationContext } from '../../models/application-context.model';
import { Query } from '@datorama/akita';
import { Injectable } from '@angular/core';
import { User } from 'src/app/common/models/user.model';
import { map, skipWhile } from 'rxjs/operators';
import { Role } from 'src/app/common/models/role.model';
import { Observable } from 'rxjs';
import { ApplicationContextStore } from './application-context.store';

@Injectable({
    providedIn: 'root'
})
export class ApplicationContextQuery extends Query<ApplicationContext> {

    constructor(protected store: ApplicationContextStore) {
        super(store);
    }

    getCurrentUserObservable(): Observable<User> {
        return this.select('currentUser');
    }

    getCurrentUserRoles(): Observable<Role[]> {
        return this.select('currentUser').pipe(
            skipWhile(user => !user),
            map(user => user.getRoles())
        );
    }

    getCurrentUserRole(): Observable<Role['code']> {
        return this.select('currentUserRoleCode');
    }
}
